import { l as lucia } from "../../../chunks/auth.js";
import { r as redirect, f as fail } from "../../../chunks/index.js";
import { p as pool } from "../../../chunks/db.js";
import { Argon2id } from "oslo/password";
const load = async (event) => {
  if (event.locals.user)
    redirect(302, "/profile");
  return {};
};
const actions = {
  default: async (event) => {
    const formData = await event.request.formData();
    const username = formData.get("username");
    const password = formData.get("password");
    const page_redirect = formData.get("visit") || "";
    console.log(page_redirect);
    const confirm_password = formData.get("confirm_password");
    if (confirm_password !== password)
      return fail(400, {
        message: "Password don't match"
      });
    if (typeof username !== "string" || username.length < 3 || username.length > 31 || !/^[a-z0-9_-]+$/.test(username))
      return fail(400, {
        message: "Invalid username"
      });
    if (typeof password !== "string" || password.length < 6 || password.length > 255)
      return fail(400, {
        message: "Invalid password"
      });
    const [rows] = await pool.execute("select * from users where username = ?", [username]);
    const existingUser = rows[0];
    if (existingUser)
      return fail(400, {
        message: "Username already in use"
      });
    const hashedPassword = await new Argon2id().hash(password);
    await pool.execute("insert into users (username, password) values (?, ?)", [username, hashedPassword]);
    const { id } = (await pool.execute("select * from users where username = ?", [username]))[0][0];
    const session = await lucia.createSession(`${id}`, {});
    const sessionCookie = lucia.createSessionCookie(session.id);
    event.cookies.set(sessionCookie.name, sessionCookie.value, {
      path: ".",
      ...sessionCookie.attributes
    });
    redirect(302, `/${page_redirect}`);
  }
};
export {
  actions,
  load
};
